/* Styling for our main navbar */

/* Makes button fit to size of content */
.btn{
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
  }

.navbar{
  z-index: 1000;
}
/* Styling for our dropdown menu */

/* Main div component */
.menu{
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;
}

/* Actual dropdown menu that displays */
.dropdown-menu{
    margin-top: -0.1rem;
    display: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 1rem;
    border-radius: 0;
}

/* Logic to make dropdown menu function */
.menu:hover .dropdown-menu, .menu:active .dropdown-menu {
    display: block;
}

/* Removes transition effect from button else it would affect how menu will look when toggling dark mode */
.btn{
    transition: none !important;
}
.footer-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer-text{
    text-align: center;
    padding: 0.5rem;
}
